@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header{
    height:100vh;
    padding-top: 2rem;
    overflow: hidden;
}
.header__container{
    height:100%;
    text-align: center;
    position:relative;
}
.CTA{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 1rem;
}

.header__social::after{
    content:'';
    width: 1px;
    height:2rem;
    -webkit-columns: var(--color-primary);
            columns: var(--color-primary);
}
.me{
    /* background:linear-gradient(var(--color-primary),transparent); */
    height:40rem;
    width: 32rem;
    position: absolute;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 1.5rem;
}
.scroll__down{
    position: absolute;
    right:-2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* medium devices */
  
@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
    
   
 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    header{
        height: 100vh;
        margin-top: 1rem;
        
    }
    .header__social,.scroll__down{
        display: none;
    }
    
 }
nav{
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a{
    background:transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0, 0, 0, 0.3);

}

nav a.active{
    background: var(--color-primary);
    color: var(--color-white);
    
}

.container_about{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap:15%;
    gap:15%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background:linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}
.about_me_img{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition:var(--transition) ;
    -webkit-animation-name: pic;
            animation-name: pic;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
    -webkit-animation-play-state: running;
            animation-play-state: running;
   
    
}
@-webkit-keyframes pic {
    from {-webkit-transform: rotate(-10deg);transform: rotate(-10deg)}
    to {-webkit-transform: rotate(10deg);transform: rotate(10deg)}
  }
@keyframes pic {
    from {-webkit-transform: rotate(-10deg);transform: rotate(-10deg)}
    to {-webkit-transform: rotate(10deg);transform: rotate(10deg)}
  }
.about_me_img:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(e,1fr);
    grid-gap:1.5rem;
    gap:1.5rem;
}

.about_card{
    background:var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5{
    font-size: 0.9rem;
}

.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    color:var(--color-light);

}

@media screen and (max-width:1024px){
    .container_about{
        grid-template-columns: 1fr;
        grid-gap:0;
        gap:0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
        text-align: center;
    }

 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    .about_me{
        width: 65%;
        margin:0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        grid-gap:1rem;
        gap:1rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p{
        margin: 1.5rem 0;
    }
    
 }
.container_exp{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:2rem;
    gap:2rem;
}
.container_exp > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;   
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);

}

.container_exp > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.container_exp > div > h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.exp_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.exp_detail{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;

}

.exp_icon{
    margin-top: 6px;
    color: var(--color-primary);

}

/* medium devices */
  
@media screen and (max-width:1024px){
    .container_exp{
        grid-template-columns: 1fr;
    }
    .container_exp > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .exp_content{
        padding: 1rem;

    }
 }

  /*.....small devices.... */

  @media screen and (max-width:600px){
   
    .container_exp{
        grid-gap:1rem;
        gap:1rem;

    }
    .container_exp > div {
        width: 100%;
        padding: 2rem 1rem;
    }


 }
.container_contact{
    display: grid;
    width:58%;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact_options{
    display: flex;
    flex-direction: column;
    grid-gap:1.2rem;
    gap:1.2rem;
}
.contact_opt{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid solid;
    transition: var(--transition);
}

.contact_opt:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.contact_icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact_opt > a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display: flex;
    flex-direction: column;
    grid-gap:1.2rem;
    gap:1.2rem;

}

input,textarea{
    width :100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background:transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}


/* medium devices */
  
@media screen and (max-width:1024px){
    .container_contact{
        grid-template-columns: 1fr;
        grid-gap:2rem;
        gap:2rem;
    }
 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    .container_contact{
        width:var(--container-width-sm);
    }
   form{
        height :80%;
    }
 }
footer{
    background: var(--color-primary);
    padding: 3rem 0;
