}

.footer_content{
    display: flex;
    flex-wrap: wrap;
    grid-gap:2rem;
    gap:2rem;
    margin :0 auto 5rem;
    justify-content: center;
    
}
li> a{
    color: var(--color-bg);
}

/* medium devices */
  
@media screen and (max-width:1024px){
    
 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    .footer_content{
        flex-direction: column;
        grid-gap:1.5rem;
        gap:1.5rem;
        
    }
 }
.container_portfolio{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:2.5rem;
    gap:2.5rem;
}
.portfolio_item{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);

}
.portfolio_item:hover{
    border-color: var(--color-primary-varaint);
    background: transparent;

}
.portfolio_item_img{
    border-radius: 1.5rem;
    overflow: hidden;

}
.portfolio_item > h3{
    margin:1.2rem 0 2rem;
}
.portfolio_cta{
    
    display: flex;
    grid-gap:1rem;
    gap:1rem;
    margin: 1rem;
}

/* medium devices */
  
@media screen and (max-width:1024px){
    .container_portfolio{
        grid-template-columns: 1fr 1fr;
        grid-gap:1.2rem;
        gap:1.2rem;
    }
 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    .container_portfolio{
        grid-template-columns: 1fr;
        grid-gap:1rem;
        gap:1rem;
    }
 }
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* css variables */

:root{
    --color-bg:#0a0d19; 
    --color-bg-variant:#354080;
    --color-primary:#8799ff;
    --color-primary-variant:#b4bfff;
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);

    

    --transition: all 400ms ease;

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;

}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{    
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    background: #0a0d19;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

.container{
    width:75%;
    width:var(--container-width-lg);
    margin: 0 auto;
}
h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    
}

section > h2, section > h5{
 text-align: center;
 color: rgba(255,255,255,0.6);
 color: var(--color-light);
}
section > h2{
    color: #8799ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}
 .text-light{
    color: rgba(255,255,255,0.6);
    color: var(--color-light);
 }
 
 a{
    color: #8799ff;
    color: var(--color-primary);
    transition:all 400ms ease;
    transition:var(--transition);
 }

 a:hover{
    color: #fff;
    color: var(--color-white);
 }

 .btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #8799ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border:solid;
    border-radius: 0.4rem;
    cursor: pointer;
    border-color:#8799ff;
    border-color:var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
 }
 .btn:hover{
    background: #fff;
    background: var(--color-white);
    color: #0a0d19;
    color: var(--color-bg);
    border-color: transparent;
 }
 .btn-primary{
    background: #8799ff;
    background: var(--color-primary);
    color: #0a0d19;
    color: var(--color-bg);
 }

 img{
    display: block;
    width: 100%;
    object-fit: cover;
 }

 /* medium devices */
  
 @media screen and (max-width:1024px){
    .container{
        width: 86%;
        width: var(--container-width-md);
        margin-top: 1rem;

    }
    section{
        margin-top: 6rem;
    }
 }
  /*.....small devices.... */

  @media screen and (max-width:600px){
    .container{
        width: 90%;
        width: var(--container-width-sm);
    }
    section > h2{
        margin-bottom: 2rem;
    }
 }
